import Vue from 'vue'
import Router from 'vue-router'
import Todo from '@/todo.vue'
import Menu from '@/menu.vue'

Vue.use(Router)

export default new Router({
  mode: 'history', // 使用 HTML5 的 history 模式，不想要 # 的話就加這行
  routes: [
    {
      path: '/',
      name: 'todo',
      component: Todo
    },
    {
      path: '/Menu',
      name: 'menu',
      component: Menu
    }
  ]
})