<template>
  <div id="menu">
    <h1>📋Menu</h1>
    <div class="input_area">
      <select v-model="index">
          <option
              v-for="(item, index) in list"
              :key="item.name"
              :value="index"
          >{{ item.name }}</option>
      </select>

      <button @click="random">Shuffle</button>
      <button @click="add_task">Add Todo</button>
      <span v-if="show_check" class="checkmark">✔</span>
    </div>
    <div class="info_area">
      <template v-if="list[index]">
        <div class="info add">🏠 {{ list[index].add }}</div>
        <div class="info phone">📞 {{ list[index].phone }}</div>
        <img :src="list[index].img">
      </template>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {

  data: function() {
    return {
      list: [],
      index: 0,
      show_check: false
    }
  },

  methods: {
    ...mapActions(['add_item', 'get_task_id', 'remember', 'get_todo']),

    random: function(){
      this.index = Math.floor(Math.random() * this.list.length)
    },

    add_task: async function() {
      let restaurant = this.list[this.index];

      if(restaurant){
        let task = {
          id: await this.get_task_id(),
          text: '向' + restaurant.name + '訂餐',
          done: false,
          edit: false
        };

        this.add_item(task);

        this.show_check = true;
        setTimeout(() => {
          this.show_check = false;
        }, 1500);
      }
      
    },

  },

  created: function(){
    let url = 'https://script.google.com/macros/s/AKfycbza0u6xP7rpEhD0NgOVeJlu5nrxiPV0CktKtfan74sKU35pHzrw/exec';

    this.$http.get(url).then(res => {
        let $data = res.data,
            $list = $data.map(data => {
                return {
                    name: data[0],
                    add: data[2],
                    phone: data[3],
                    img: data[4]
                }
            });

        this.list = $list;

    }).catch(error => {
        console.error(error);
    });
  }
}
</script>

<style lang="scss">

body{
  margin: 0;
  background-color: #E5F3FD;
}

#app {
  color: #2234A8;
  max-width: 50%;
  margin: 40px auto;
  font-family: 'calibri';
  text-align: center;

  .input_area {
    display: flex;
    justify-content: center;
    gap: 10px;
    padding: 10px;
    margin: 20px auto;
    font-size: 16px;
    flex-grow: 1;

    select {
      display: flex;
      font-size: 1em;
      flex-grow: 1;
      width: 80%;
      border: 0em;
      border-radius: 4px;
    }
  }

  button {
    color: #2234A8;
    padding: 10px 15px;
    font-size: 1em;
    cursor: pointer;
    background-color: #ffff;
    border: 0em;
    border-radius: 4px;
    white-space: nowrap;
  }

  ul {
    list-style: none;
    padding: 10px;
    text-align: center;
    margin: 0;
  }

  .info_area {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-content: space-between;
    background-color: #ffff;
    border-radius: 4px;
    gap: 10px;
    padding: 20px;
    margin: 20px 10px 20px 10px;
    overflow: hidden;
  }

  .add-todo-wrapper {
    position: relative;
    display: inline-flex;
    align-items: center;
    gap: 8px;
  }

  .checkmark {
    color: #00bb27;
    font-size: 1.5em;
    animation: rotate-in 0.6s ease-out;
    display: inline-block;
  }

  @keyframes rotate-in {
    0% {
      opacity: 0;
      transform: rotate(-180deg) scale(0);
    }
    50% {
      opacity: 1;
      transform: rotate(10deg) scale(1.1);
    }
    100% {
      transform: rotate(0deg) scale(1);
    }
  }

}
</style>