<template>
  <div id="todo">
    <h1>📝 Todo List</h1>

    <div class="input_area">
      <input 
        v-model="new_task" 
        type="text"
        placeholder="add a new task..." 
        @keyup.enter="add_task"/>
        

      <button @click="add_task">Add</button>
    </div>

    <ul v-if="todos.length">
      <li
          is="todo_item"
          v-for="todo in todos"
          :key="todo.id"
          :node="todo"
          @remove_task="remove_task"
          @edit_task="edit_task"
          @save_task="save_task"
          @remember_list="remember_list"
      >
      </li>
    </ul>

    <p v-else>Todo list is empty.</p>

  </div>
</template>

<script>
import todo_item from './components/todo_item.vue'
import { mapActions, mapState } from 'vuex'

export default {
  name: 'App',

  components: {
    todo_item
  },

  data: function() {
    return {
      new_task: ''
    };
  },

  computed: {
    ...mapState(['todos'])
  },

  methods: {
    ...mapActions(['add_item', 'get_task_id', 'remember', 'get_todo']),

    add_task: async function() {
      if (this.new_task.trim() !== '') {
        let task = {
          id: await this.get_task_id(),
          text: this.new_task.trim(),
          done: false,
          edit: false
        };

        this.add_item(task); 
        this.new_task = '';
      }
    },

    remember_list: function(){
      let $todos = this.todos,
          _text = JSON.stringify($todos);

      $cookies.set('todos', _text);
    },

    remove_task(todo) {
      let _index = this.todos.indexOf(todo);

      if( _index > -1 ) {
        this.todos.splice(_index, 1);

        this.remember();
      }
    },

    edit_task:function(todo) {
      todo.edit = true;
    },

    save_task:function(todo) {
      todo.edit = false;

      this.remember();
    }
  },

  created: function() {
    this.get_todo();
    console.log('success')
  }
}
</script>

<style lang="scss">

body{
  margin: 0;
  background-color: #E5F3FD;
}

#app {
  color: #2234A8;
  max-width: 50%;
  margin: 40px auto;
  font-family: 'calibri';
  text-align: center;

  .input_area {
    display: flex;
    justify-content: flex-start;
    gap: 10px;
    padding: 10px;
    margin: 20px auto;
    font-size: 16px;

    input {
    display: flex;
    font-size: 1em;
    flex-grow: 1;
    width: 80%;
    border: 0em;
    border-radius: 4px;
    }
  }

  button {
    color: #2234A8;
    padding: 10px 15px;
    font-size: 1em;
    cursor: pointer;
    background-color: #ffff;
    border: 0em;
    border-radius: 4px;
  }

  ul {
    list-style: none;
    padding: 10px;
    text-align: center;
    margin: 0;
  }
}
</style>