import Vue from 'vue'
import Vuex from 'vuex'
import VueCookies from 'vue-cookies'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        todos: []
    },

    mutations: {
        remember: function (state) {
            let $todos = state.todos,
                task_item = JSON.stringify($todos);

            VueCookies.set('todos', task_item);
        },

        add_task: function(state, task) {
            state.todos.push(task)
        },

        todo_list: function(state) {
            let todos = VueCookies.get('todos') || []
            state.todos = todos;
        },
    },

    actions: {
        add_item({ commit }, todo) {
            commit('add_task', todo)
            commit('remember')
        },

        get_task_id: async () => {
            return Math.floor(Math.random() * 10000)
        }, 

        get_todo({ commit }) {
            commit('todo_list')
        },

        remember({ commit }) {
            commit('remember')
        }
    }
})